@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;1,500&family=Playfair+Display+SC:ital@1&display=swap);
#item-list{list-style-type:none;padding:0;margin:0;height:70vh;overflow:scroll;max-height:32em;overflow:scroll}#item-list li{padding:3px 8px 8px;border-bottom:1px solid #353535;font-size:0.7em}#item-list li a{margin:0;text-decoration:none;display:flex;flex-wrap:wrap;justify-content:space-between}#item-list li a:link,#item-list li a:visited{color:inherit}#item-list li a:hover,#item-list li a:focus{text-decoration:underline}#item-list p{margin:0}#item-list p:nth-child(3){width:100%}#item-list p.item-badges{margin-top:.5em}.item-title{max-width:85%}.item-title span{color:gray}.item-systems{height:1.5em;min-width:1em}

